<template>
    <div class="root">
        <header id="head_top1">
            <section class="head_goback1" @click="goBack">
                <i class="el-icon-arrow-left"></i>
            </section>
            <section class="title_head1 ellipsis">
                <span class="title_text1">
                    添加地址
                </span>
            </section>
        </header>
        <section class="page_text_container" v-if="show1">
            <section class="section_list">
                <span class="section_left">联系人</span>
                <section class="section_right">
                    <input type="text" placeholder="你的名字" class="input_style" v-model="iptName">
                    <div class="choose_sex">
                        <span class="choose_option" >
                            <i class="el-icon-success" @click="show=true" :class="{color:show}"></i>
                            <span>先生</span>
                        </span>
                        <span class="choose_option" >
                             <i class="el-icon-success" @click="show=false" :class="{color:!show}"></i>
                            <span>女士</span>
                        </span>
                    </div>
                </section>
            </section>
            <section class="section_list">
                <span class="section_left">联系电话</span>
                <section class="section_right">
                    <div class="phone_add">
                        <input type="text" placeholder="你的手机号" class="input_style" v-model="iptPhone">
                        <i class="el-icon-plus"></i>
                    </div>
                </section>
            </section>
            <section class="section_list">
                <span class="section_left">送餐地址</span>
                <section class="section_right">
                    <div class="choose_address" @click="searchAddresss">{{chooseAddress.name?chooseAddress.name:'小区/写字楼/学校等'}}</div>
                    <input type="text" placeholder="详细地址(如门牌号等)" class="input_style" v-model="iptAddress">
                </section>
            </section>
            <section class="section_list">
                <span class="section_left">标签</span>
                <section class="section_right">
                    <input type="text" placeholder="无/家/学校/公司" class="input_style" v-model="iptTag">
                </section>
            </section>

        </section>
        <div class="determine" v-if="show1" @click="addAddress">确定</div>
        <div v-if="!show1">
            <section class="">
                <header id="head_top">
                    <section class="head_goback" @click="goBack">
                        <i class="el-icon-arrow-left"></i>
                    </section>
                    <section class="title_head ellipsis">
                        <span class="title_text">
                            搜索
                        </span>
                    </section>
                </header>
                <div class="search_form">
                    <input type="text" class="" placeholder="请输入小区/写字楼/学校等" v-model="ipt">
                    <button @click="searchBtn">搜索</button>
                </div>
                <div class="empty_tips" v-if="show2">
                    <p>找不到地址?</p>
                    <p>尝试输入小区、写字楼或学校名</p>
                    <p>详细地址(如门牌号)可稍后输入哦</p>
                </div>
                <ul class="address_list">
                    <li v-for="(v,k) in address" :key="v.name" @click="chooseAdress(v)">
                        <h4>{{v.name}}</h4>
                        <p>{{v.address}}</p>
                    </li>
                </ul>
            </section>
        </div>
        <div class="alet_container" v-if="!show3">
            <section class="tip_text_container">
                <div class="tip_icon">
                    <span></span>
                    <span></span>
                </div>
                <p class="tip_text">请输入姓名</p>
                <div class="confrim" @click="show3=true">确认</div>
            </section>
        </div>
        <div class="alet_container" v-if="!show4">
            <section class="tip_text_container">
                <div class="tip_icon">
                    <span></span>
                    <span></span>
                </div>
                <p class="tip_text">请输入手机号</p>
                <div class="confrim" @click="show4=true">确认</div>
            </section>
        </div>
        <div class="alet_container" v-if="!show5">
            <section class="tip_text_container">
                <div class="tip_icon">
                    <span></span>
                    <span></span>
                </div>
                <p class="tip_text">详细地址信息错误</p>
                <div class="confrim" @click="show6=true">确认</div>
            </section>
        </div>
        <div class="alet_container" v-if="!show6">
            <section class="tip_text_container">
                <div class="tip_icon">
                    <span></span>
                    <span></span>
                </div>
                <p class="tip_text">标签错误</p>
                <div class="confrim" @click="show7=true">确认</div>
            </section>
        </div>
        <div class="alet_container" v-if="!show7">
            <section class="tip_text_container">
                <div class="tip_icon">
                    <span></span>
                    <span></span>
                </div>
                <p class="tip_text">请登录</p>
                <div class="confrim" @click="show7=true">确认</div>
            </section>
        </div>
    </div>
</template>

<script>
    export default {
        name: "AddAddress",
        data(){
            return{
                show:true,
                show1:true,
                show2:true,
                show3:true,
                show4:true,
                show5:true,
                show6:true,
                show7:true,
                ipt:"",
                user:[],
                address:[],
                iptName:"",
                iptPhone:"",
                iptAddress:"",
                iptTag:"",
                sex:1,
                chooseAddress:[],
                tag_type:0,
            }
        },
        created(){
            if (localStorage.getItem('user')){
                this.user = JSON.parse(localStorage.getItem('user'));
            }
        },
        methods:{
            goBack(){
                this.$router.go(-1);
            },
            searchAddresss(){
                this.show1 = false;
            },
            searchBtn(){
                this.show2 = false;
                this.axios.get("v1/pois?city_id="+this.user.user_id+"type=nearby&keyword="+this.ipt).then(res=>{
                    this.address = res;
                    console.log(res);
                }).catch(err=>{

                })
            },
            addAddress(){

                if (this.iptName==""){
                    this.show3=false;
                }
                if (this.iptPhone==""){
                    this.show4=false;
                }
                if (this.iptAddress==""){
                    this.show5=false;
                }
                if (this.iptTag==""){
                    this.show6=false;
                }
                if (this.show){
                    this.sex = 1
                }else {
                    this.sex = 2
                }
                if (this.iptTag=='家'){
                    this.tag_type = 2
                } else if (this.iptTag=='学校'){
                    this.tag_type = 3
                } else if (this.iptTag=='公司'){
                    this.tag_type = 4
                }
                if (localStorage.getItem('user') == null){
                    this.show7=false;
                }
                if (this.iptName&&this.iptPhone&&this.iptAddress&&this.iptTag){
                    this.axios.post("v1/users/"+this.user[0].user_id+"/addresses?address="+this.chooseAddress.name+"&address_detail="+this.iptAddress+"&geohash="+this.chooseAddress.geohash+"&name="+this.iptName+"&phone="+this.iptPhone+"&tag="+this.iptTag+"&sex="+this.sex+"&phone_bk=1234567"+"&tag_type="+this.tag_type).then(res=>{
                        console.log(res)
                    }).catch(err=>{

                    })
                }

            },
            chooseAdress(v){
                this.chooseAddress = v;
                console.log(v);
                this.show1 = true;
            }
        }
    }
</script>

